import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import $http from '@/common/api/request.js'
import Swiper from '@/components/index/indexSwiper.vue'
import Recommend from '@/components/index/Recommend.vue'
import Card from '@/components/common/Card.vue'
import GoodList from '@/components/common/GoodList.vue'
import Banner from '@/components/index/banner.vue'
import Icons from '@/components/index/Icons.vue'
import Hots from '@/components/index/Hots.vue'
import Shop from '@/components/index/Shop.vue'
export default {
	components: {
		Swiper,
		Recommend,
		Card,
		GoodList,
		Banner,
		Icons,
		Hots,
		Shop,
		uniLoadMore
	},
	data() {
		return {
			tabBarIndex: 0,
			ScrollIndex: 'top0',
			ClientHeight: 0,
			status: 'more',
			topBar: [],
			// 承载数据
			newTopBar: []
		}
	},
	onLoad() {
		this.getindexData()
	},
	onReady() {
		// 自定义组件无效,此方法(暂时),无法获取ClientHeight
		// const query = uni.createSelectorQuery().in(this);
		// query.select('.home-nav').boundingClientRect(data => {
		// 	// this.ClientHeight = data.height
		// 	this.ClientHeight = 1300
		// }).exec();
		// 获取系统高度
		uni.getSystemInfo({
			success: (res) => {
				this.ClientHeight = res.windowHeight - uni.upx2px(80) - this.getClientHeight()
			}
		})
	},
	// 监听标题按钮点击事件
	onNavigationBarButtonTap(e){
		console.log(e)
		if(e.float=='left'){
			uni.navigateTo({
			    url: '../search/search'
			})
		}
	},
	methods: {
		// 监听顶部滑块切换事件
		tabBarClick(index) {
			this.tabBarIndex = index
			this.ScrollIndex = 'top' + index
			if (this.newTopBar[index].loadding == 'first') {
				this.getindexOtherData()
			}
		},
		// 监听内容滑块切换事件
		SwiperChange(e) {
			this.tabBarClick(e.detail.current);
		},
		initData(res) {
			let arr = []
			this.topBar.forEach((item, i) => {
				let obj = {
					data: [],
					loadding: 'first',
				}
				if (i == 0) {
					obj.data = res.data
					// console.log(res.data)
				}
				arr.push(obj)
			})
			return arr
		},
		// 监听上拉加载
		loadmore(index) {
			this.status = 'loading'
			setTimeout(()=> {
				this.getindexOtherData(() => {
					this.status = 'more'
				})
			}, 2000);
		},
		//监听客户端，兼容
		getClientHeight() {
			const res = uni.getSystemInfoSync();
			const system = res.platform;
			console.log(system)
			if (system === 'ios') {
				// return 44+res.statusBarHeight;
				return 0;
			} else if (system === 'android') {
				// return 48+res.statusBarHeight;
				return 0;
			} else {
				return 0;
			}
		},
		/* 
		网络请求事件监听
		 */
		// 请求首页数据
		getindexData() {
			$http.request({
				url: '/index/data',

			}).then((res) => {
				this.topBar = res.topBar;
				this.newTopBar = this.initData(res);
			}).catch(() => {
				uni.showToast({
					title: '请求失败',
					icon: 'none'
				})
			})
		},
		// 其他模板首页数据
		getindexOtherData(callback) {
			let index = this.tabBarIndex
			let topBarid = this.topBar[index].id
			console.log(this.newTopBar)
			let pageId = Math.ceil(this.newTopBar[index].data.length / 5) + 1;
			$http.request({
				url: `/index/${topBarid}/data/${pageId}` /* ES6*/
			}).then(res => {
				this.newTopBar[index].data = [...this.newTopBar[index].data, ...res]
			}).catch(()=>{
				this.status = 'nomore'
			})
			// 请求结束后，关上loadding
			this.newTopBar[index].loadding = 'last'
			if (typeof callback === 'function') {
				callback();
			}
		}
	}
}
